function loadView (view) {
  return () =>
    import(/* webpackChunkName: "view-[request]" */ '@/views/' + view + '.vue')
}

function loadLayout (layout) {
  return () =>
    import('@/components/layout/' + layout + '.vue')
}

function homeMain (view) {
  return {
    default: loadView(view)
  }
}

const parent = (parentName) => {
  return {
    default: {
      name: parentName,
      template: '<router-view></router-view>'
    },
    header: loadLayout('main-navbar'),
    footer: loadLayout('main-footer')
  }
}

// 라우트 대상 정의 (url추가시 여기에)
const routes = [
  {
    path: '/',
    name: 'home-main',
    components: homeMain('home-main'),
    meta: {
      title: '건프라 리스트'
    }
  },
  {
    path: '/login',
    name: 'user-login',
    component: loadView('user-login'),
    meta: {
      title: '로그인'
    }
  },
  {
    path: '/register',
    name: 'register',
    component: loadView('user-register'),
    meta: {
      title: '회원가입'
    }
  },
  {
    path: '/reset-password',
    name: 'reset-password',
    component: loadView('user-reset-password'),
    meta: {
      title: '비밀번호 찾기 및 변경'
    }
  },
  {
    path: '/user',
    name: 'user',
    components: parent('user'),
    children: [
      {
        path: '/daily-proverb',
        name: 'user-daily-proverb',
        component: loadView('user-daily-proverb'),
        meta: {
          title: '오늘의 명언'
        }
      },
      {
        path: '/proverb-histories',
        name: 'user-proverb-histories',
        component: loadView('user-proverb-histories'),
        meta: {
          title: '명언 다시보기'
        }
      }
    ]
  }
]

routes.push({
  path: '/:catchAll(.*)',
  redirect: {
    name: 'home-main'
  }
})

export default routes
